<template>
  <div>
    <van-nav-bar :title="mode==='add'?'添加地址':'编辑地址'"  left-arrow @click-left="$router.replace('/addressList')" fixed z-index="100"  placeholder :border="false" />
    <div class="form-box">
      <div class="item">
        <div class="content">
          <div class="label">姓名</div>
          <div class="input-box">
            <input type="text" v-model="form.name" placeholder="收货人姓名" class="input">
          </div>
        </div>
      </div>
      <div class="item">
        <div class="content">
          <div class="label">电话</div>
          <div class="input-box">
            <input type="text" v-model="form.phone" placeholder="收货人手机号" class="input">
          </div>
        </div>
      </div>
      <div class="item region-box" @click="showPopup">
        <div class="content">
          <div>
            <div class="label">地区</div>
            <div class="input-box">
              <div :class="{'text': true, 'color': form.region.length>0 }">{{ areaText }}</div>
            </div>
          </div>
          <van-icon name="arrow" class="icon-arrow" />
        </div>
      </div>
      <div class="item detail-box">
        <div class="content">
          <div class="label">详细地址</div>
          <div class="input-box">
            <textarea v-model="form.detail" class="textarea" :style="{height:(styleHeight*styleNum)+'px'}" @input="addressDetail" placeholder="街道门牌、楼层房间号等信息"></textarea>
          </div>
        </div>
      </div>
      <div class="item default-box">
        <div class="content">
          <div class="label">设为默认收货地址</div>
          <van-switch v-model="form.isdefault" size="24" active-color="orange" />
        </div>
      </div>
      <button class="btn save" @click="save" v-if="mode==='add'">保存</button>
      <button class="btn save" @click="edit" v-else>修改</button>
      <button class="btn del" v-if="mode==='edit'" @click="delAddress">删除</button>
    </div>
    <van-popup v-model="isShowPopup" position="bottom">
      <van-picker show-toolbar  :columns="arealist"  @confirm="onConfirm" @cancel="onCancel" />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: 'AddAddress',
  data () {
    return {
      form: {
        name: '',
        phone: '',
        detail: '',
        region: [],
        isdefault: false
      },
      styleNum: 1,
      styleHeight: 20,
      arealist: [],
      isShowPopup: false,
      addressId: NaN
    }
  },
  created () {
    this.getRegion()
    if (this.$route.query.addressId) {
      this.addressId = Number(this.$route.query.addressId)
    }
  },
  computed: {
    mode () {
      return this.$route.query.mode
    },
    areaText () {
      const { region } = this.form
      return region.length === 0 ? '选择省 / 市 / 区' : region[0].label + region[1].label + region[2].label
    }
  },
  watch: {
    addressId (newVal, oldVal) {
      this.getAddressDetail(newVal)
    }
  },
  methods: {
    showPopup () {
      this.isShowPopup = true
    },
    onCancel () {
      this.isShowPopup = false
    },
    onConfirm (valArr, idArr) {
      this.form.region = [
        {
          value: this.arealist[idArr[0]].id,
          label: valArr[0]
        },
        {
          value: this.arealist[idArr[0]].children[idArr[1]].id,
          label: valArr[1]
        },
        {
          value: this.arealist[idArr[0]].children[idArr[1]].children[idArr[2]].id,
          label: valArr[2]
        }
      ]
      this.isShowPopup = false
    },
    addressDetail (e) {
      const val = e.target.value
      if (val.length > 0) {
        this.styleNum = Math.ceil(val.length / 18)
      } else {
        this.styleNum = 1
      }
    },
    async getRegion () {
      const { data: { list } } = await this.$api.address.getRegion()
      this.arealist = Object.values(list).map(item => {
        item = {
          id: item.id,
          text: item.name,
          children: Object.values(item.city).map(ele => {
            ele = {
              id: ele.id,
              text: ele.name,
              children: Object.values(ele.region).map(i => {
                i = {
                  id: i.id,
                  text: i.name
                }
                return i
              })
            }
            return ele
          })
        }
        return item
      })
    },
    rule () {
      const regPhone = /^1[3-9]\d{9}$/
      if (!this.form.name) {
        this.$toast('请输入收货人姓名')
        return false
      }
      if (!this.form.phone) {
        this.$toast('请输入手机号')
        return false
      }
      if (!regPhone.test(this.form.phone)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (this.form.region.length === 0) {
        this.$toast('请选择地区')
        return false
      }
      if (!this.form.detail) {
        this.$toast('请输入详细地址')
        return false
      }
      return true
    },
    save () {
      if (this.rule()) {
        this.$store.dispatch('address/addAddress', this.form)
      }
    },
    edit () {
      if (this.rule()) {
        const obj = {
          addressId: this.addressId,
          form: this.form
        }
        this.$store.dispatch('address/editAddress', obj)
      }
    },
    async delAddress () {
      await this.$api.address.delAddress(this.addressId)
      this.$toast.success('删除成功')
      const t = setTimeout(() => {
        this.$router.replace('/addressList')
        clearTimeout(t)
      }, 1000)
    },
    async getAddressDetail (addressId) {
      const { data: { detail } } = await this.$api.address.getAddressDetail(addressId)
      this.form = {
        name: detail.name,
        phone: detail.phone,
        detail: detail.detail,
        region: [
          {
            value: detail.province_id,
            label: detail.region.province
          },
          {
            value: detail.city_id,
            label: detail.region.city
          },
          {
            value: detail.region_id,
            label: detail.region.region
          }
        ],
        isdefault: this.$store.state.address.defaultId === detail.address_id
      }
    }
  }
}
</script>

<style lang="less" scoped>
.form-box{
  padding: 15px;
  .item{
    padding: 13px 0;
    border-bottom: 1px solid #f8f7f7;
    &:last-of-type{
      border: 0;
    }
    .content{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      font-size: 15px;
      .label{
        width: 75px;
        color: #666;
      }
      .input-box{
        flex: 1;
        display: flex;
        input{
          width: 100%;
        }
        textarea{
          width: 100%;
        }
        .text{
          color: #ccc;
          &.color{
            color: #333;
          }
        }
      }
    }
  }
  .region-box{
    .content{
      justify-content: space-between;
      >div{
        display: flex;
        align-items: center;
        justify-content: flex-start;
      }
    }
  }
  .detail-box{
    .content{
      align-items: flex-start;
    }
  }
  .default-box{
    .content{
      justify-content: space-between;
      align-items: center;
      .label{
        width: auto;
      }
    }
  }
  .btn{
    width: 100%;
    height: 44px;
    border-radius: 30px;
    font-size: 15px;
  }
  .save{
    color: #fff;
    background: linear-gradient(to right, #ff6034, #ee0a24);
    margin-top: 30px;
  }
  .del{
    border: 1px solid  #ebedf0;
    background-color: #fff;
    margin-top: 15px;
  }
}
</style>
